.cat-padding{
  padding: 5px 6px;
}
.btn{
  border: none;
  font-weight: bold;
}
.btn-primary{
  background-color: rgb(0, 231, 231);
  color: black;
}
.btn-primary:hover{
  background-color: #000000;
  color:rgb(0, 231, 231) ;
}
.btn-secondary:hover{
  background-color:#000000 ;
  color: white;
}
.btn-secondary{
  background-color: white;
  color: #000000;
}
.btn-danger{
  background-color: rebeccapurple;
}
.white{
  color: white;
}
.padding-top{
  padding-top: 20px;
}
.nav-link{
  font-size: 19px;
}
span{
  font-size: 17px;
}
.bold{
  font-weight: bold;
}
h1, h2, h4, h6, h5, p, span{
  color: white;
}
.card-title{
  font-size: 24px;
  font-weight: 600;
}
.card-text{
  font-size: 18px;
  font-weight: 500;
}
.font-size{
  font-size: 28px;
}
.small-size{
  font-size: 20px;
}
.margin-top{
  margin-top: 20px;
}
.margin-left, .margin-right{
  margin: auto 10px
}
.transparent{
  background: transparent;
  border: 2px solid rgba(238, 248, 247, 0.2);
  backdrop-filter: blur(1px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  color: #ffffff;
  border-radius: 20px;
  padding: 6px;
  margin-bottom: 10px;

}
hr{

  border: 2px solid white;
}
.card{
  background: transparent;
  border: 2px solid rgba(238, 248, 247, 0.2);
  backdrop-filter: blur(8px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  color: #000000;
  border-radius: 20px;
  padding: 50px 10px 35px 10px;

}
.head-img-container{
  position: relative;
  background: transparent;
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px rgb(255, 255, 255);
  border-radius: 20px;
  display: flex;
   place-content: space-between;
   place-items: center;
  padding: 40px 80px;
}
.head-img-container h1{
  color: #ffffff;

  font-size: 70px;
  font-family: 'Times New Roman', Times, serif
}
.head-img-container span{
  color: green;
  font-size: 80px;
}
.head-img-container p{
  font-size: 18px;
font-family: calibri;
color: rgb(255, 255, 255);
}
.head-img{
  padding-top: 20px;
  height: 500px;
  width: 500px;
  object-fit: fill;
  border-radius: 20px;
}
main{

  background-image: url(../../client/public/images/back.jpg) ;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: josefin sans;
}
.footer{
  color: Black;
  padding: 12px;
  background-color: rgb(152, 194, 140);
  position: relative;
  bottom: 0px;

}
.padding{
  padding: 8px 9.3px;
  margin: auto 11.7px
}
.footer a{
  text-decoration: none;
  color: Black;
  padding: 10px;
}

.footer a:hover{
  color: #ffffff;
  border-bottom: 1px solid #ffefba;
}
