.register{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url(../../../public/images/bg5.jpg) ;
    color: aliceblue;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 81vh;
}
.register-wrapper{
width: 480px;
display: flex;
flex-direction: column;
background: transparent;
border: 2px solid rgba(255, 255, 255, 0.2);
backdrop-filter: blur(4px);
box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
color: #fff;
border-radius: 20px;
padding: 30px 41px 40px 41px;
}
.login-wrapper{
width: 460px;
background: transparent;
border: 2px solid rgba(255, 255, 255, 0.2);
backdrop-filter: blur(4px);
box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
color: #fff;
border-radius: 20px;
padding: 70px 40px 90px 40px;
}
.register input{
display: flex;
padding-top: 10px;
padding-left: 18px;
padding-bottom: 10px;
border-radius: 50px;
margin-bottom: 28px;
}
.register input::placeholder{
font-family: josefin sans;
font-weight: 400;

}
.page{
font-family: Comfortaa;
color: black;
font-weight: 700!important;
display: flex;
justify-content: center;
margin-bottom: 25px;
}
#login-input{
padding-top: 10px;
padding-left: 18px;
padding-bottom: 10px;
border-radius: 50px;
margin-top: 0px;
margin-bottom: 40px;
}
.login-page{
    font-family: Comfortaa;
    color: black;
    font-weight: 700!important;
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}
a{
text-decoration: underline;
color: greenyellow;
}
button{
width: 100%;
border-radius: 60px;
background-color: rgb(46, 184, 46);
color: white;
border: none;
padding-top: 10px;
padding-bottom: 10px;
}
button:hover{
background-color: green;
transition: all .2s ease-in
}
button:active{
transform: scale(.9)
}
.react-icons{
position: absolute;
color: rgb(108, 107, 107);
font-size: 20px !important;
top: 14px;
right: 26px;
}
.password-icons{
position: absolute;
color: rgb(108, 107, 107);
font-size: 20px !important;
top: 12px;
right: 26px;
}
.phone-icons{
position: absolute;
color: rgb(108, 107, 107);
font-size: 17px !important;
top: 15px;
right: 26px;
}
.address-icons{
position: absolute;
color: rgb(108, 107, 107);
font-size: 19px !important;
top: 14px;
right: 26px;
}
.input-container {
position: relative;
}
#exampleInputnewPassword1::placeholder{
    position: absolute;
    top: 43%;    
    font-size: 20px;
}